import React, {Component} from 'react'
import {connect} from 'react-redux'
import axios from 'axios'
import {fetchAutos, fetchComments, submitComment} from '../actions';
import '../styles/autoDetails.css'

class AutoDetailsPage extends Component {
    constructor() {
        super()
        this.state = {
            auto: {},
            error: null
        }

        this.renderComments = this.renderComments.bind(this)
        this.submitComment = this.submitComment.bind(this)
    }

    componentDidMount() {
        this.props.fetchComments()
        this.setState({auto: this.props.auto})
    }

    componentWillReceiveProps(nextProps) {
        this.setState({auto: nextProps.auto})
    }

    async incrementLikes(id) {
        if (!this.props.auth) {
            this.setState({error: 'Ehhez a funkcihoz be kell jelentkezni!'})
            return
        }
        await axios.put('/api/autos/likes', {id: id})
        this.setState({auto: {...this.state.auto, likes: this.state.auto.likes + 1}})
    }

    goToEditAutoPage(id) {
        if (!this.props.auth) {
            this.setState({error: 'Ehhez a funkcihoz be kell jelentkezni!'})
            return
        }

        this.props.history.push(`/autos/${id}/edit`)
    }


    async deleteAuto(id) {
        if (!this.props.auth) {
            this.setState({error: 'Ehhez a funkcihoz be kell jelentkezni!'})
            return
        }
        await axios.delete('/api/autos/delete', {data: {id: id}})
        this.props.fetchAutos()
        this.props.history.push('/')
    }

    convertPrice() {
        if (this.state.auto.ar) {
            return this.state.auto.ar.toString().replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1,")
        }
        return 0
    }

    convertUploadTime() {
        if (this.state.auto.feltoltve) {
            const date = this.state.auto.feltoltve.split('T')
            return date[0]
        }
        return ''
    }

    errorShowUp(errorMessage) {
        return (
            <div className="alert">
                <span className="closebtn" onClick={() => this.closeDialog()}>&times;</span>
                {errorMessage}
            </div>
        )
    }

    closeDialog() {
        this.setState({error: null})
    }

    renderComments() {
        let comments = []
        let commentsNum = 0
        if (this.state.auto._comments) {
            this.props.comments.map(comment => {
                return this.state.auto._comments.forEach(thisAutoComment => {
                    if (thisAutoComment === comment._id) {
                        let commentObject = Object.assign({}, comment)
                        comments.unshift(commentObject)
                        commentsNum++
                    }
                })
            })
        }
        return (
            <div className="comments">
                <h5>{commentsNum} hozzszls</h5>
                {comments.map((comment, i) => {
                    return (
                        <div className="comment" key={i} style={{ margin: '20px' }}>
                            <img src={comment.picture} alt={`${comment.name} profile`} style={{ width: '50px' }} />
                            <strong>{comment.name}</strong> - {comment.text}
                        </div>
                    )
                })}
            </div>
        )
    }


    submitComment(e) {
        e.preventDefault()
        if (this.props.auth && this.state.auto) {
            this.props.submitComment(this.props.auth._id, this.state.auto._id, this.props.auth.name, this.refs.comment.value)
            this.refs.commentForm.reset()
            this.props.fetchComments()
        } else {
            this.setState({error: 'Ehhez a funkcihoz be kell jelentkezni!'})
            document.documentElement.scrollTop = 0;
            return
        }
    }

    render() {
        let {_id, kep, modell, marka, ev, allapot, kivitel, km, szin, tomeg, uzemanyag, hengerUrTartalom, teljesitmeny, hajtas, valto, leiras, likes} = this.state.auto
        return (
            <div className="container" style={{marginTop: '40px', marginBottom: '40px'}}>
                <div className="row">

                    <div className="error">
                        {this.state.error ? this.errorShowUp(this.state.error) : null}
                    </div>

                    <h6 className="title">{marka} - {modell}</h6>
                    <div className="col s7">
                        <img className="img"
                             src={kep ? kep : "http://maestroselectronics.com/wp-content/uploads/2017/12/No_Image_Available.jpg"}
                             alt={`${marka}-${modell}`}/>
                        <ul className="points">
                            <li className="uploaded"><span>Feltltve:</span>{this.convertUploadTime()}</li>
                            <li className="text"><span>Kedvelsek:</span>{likes}</li>
                        </ul>
                    </div>
                    <div className="col s5">
                        <ul className="points">
                            <li className="text"><span>r:</span>{this.convertPrice()}<span className="unit">Ft</span>
                            </li>
                            <li className="text"><span>vjrat:</span>{ev}</li>
                            <li className="text"><span>llapot:</span>{allapot}</li>
                            <li className="text"><span>Kivitel:</span>{kivitel}</li>
                            <li className="text"><span>Km:</span>{km}</li>
                            <li className="text"><span>Szn:</span>{szin}</li>
                            <li className="text"><span>Tmeg:</span>{tomeg}<span className="unit">kg</span></li>
                            <li className="text"><span>zemanyag:</span>{uzemanyag}</li>
                            <li className="text"><span>Hengerrtartalom:</span>{hengerUrTartalom}<span
                                className="unit">cc</span></li>
                            <li className="text"><span>Teljestmny:</span>{teljesitmeny}<span
                                className="unit">le</span></li>
                            <li className="text"><span>Hajts:</span>{hajtas}</li>
                            <li className="text"><span>Vlt:</span>{valto}</li>
                            <li className="desc"><span>Lers:</span>{leiras}</li>

                            <button style={{margin: '10px', display: 'block', background: '#1565C0'}}
                                    className="waves-effect waves-light btn"
                                    onClick={() => this.incrementLikes(_id)}>Kedvels <i
                                className="icon ion-thumbsup"></i></button>
                            <button style={{margin: '10px', display: 'block', background: '#4CAF50'}}
                                    className="waves-effect waves-light btn"
                                    onClick={() => this.goToEditAutoPage(_id)}>Szerkeszts <i
                                className="icon ion-edit"></i></button>
                            <button style={{margin: '10px', display: 'block', background: '#f44336'}}
                                    className="waves-effect waves-light btn"
                                    onClick={() => this.deleteAuto(_id)}>Trls <i className="icon ion-trash-b"></i>
                            </button>

                        </ul>
                    </div>
                </div>
                {/* komment szekci */}
                <div className="comment-container">
                    {this.renderComments()}
                    <form ref="commentForm" onSubmit={(e) => this.submitComment(e)}>
                        <input type="text" ref="comment"/>
                        <button type="submit" className="btn" style={{background: '#1565C0'}}>Hozzszlok</button>
                    </form>
                </div>
            </div>
        )
    }
}

function mapStateToProps(state, ownProps) {
    const autoId = ownProps.match.params.id;
    const auto = Object.assign({}, state.autos.find(auto => auto._id === autoId))

    return {
        auto,
        auth: state.auth,
        comments: state.comments
    };
}

export default connect(mapStateToProps, {fetchAutos, fetchComments, submitComment})(AutoDetailsPage)


